	module.exports = {"data":{"contentfulChapitre":{"titreChapitre":"Chapter 10 - Éléa","texte":{"childMarkdownRemark":{"html":"<p>Once the guards took me back to my room, I noticed that my computer had been removed. Seriously, wasn't locking me in an isolation room enough? Did they really have to take my computer away? I'll end up killing someone! As soon as I see Darakei again, I kill him, I decapitate him, I open his body and I remove all his organs! I clench my fists. I need to calm down. Inspire... Expires... Inspire... Expires... It's not good for my body if I get too upset. I look towards my bedroom door and put my hand on the door.</p>\n<p>Wamien: I want to get out of here.<br>\nFemale voice: Can I help you if you want?<br>\nWamien: Who? How?</p>\n<p>I look around, I was alone in my room and the voice can't come from the hallway.</p>\n<p>Éléa: I am Éléa, I have the power to become invisible.<br>\nWamien: Hum, okay? But how do you want to help me? I can't be invisible too...<br>\nÉléa: I know the building by heart, I can drive you to the exit hiding the cameras without any problem.<br>\nWamien: I, I don't know what to say.<br>\nÉléa: Saying thank you would be a good start.<br>\nWamien: Thank you very much, shall we go?<br>\nÉléa: Give me five minutes, I'll come back for you.<br>\nWamien: Okay.</p>\n<p>The door undulates a bit as if something passes through it and then it becomes normal again. Someone came to my rescue. The door opens a little. I walk up to it and open it a little.</p>\n<p>Éléa: The coast is clear, follow me.<br>\nWamien: I don't see you, you know?<br>\nÉléa: You have thermal vision, right?<br>\nWamien: Uh, yeah, sorry.</p>\n<p>I activate my thermal vision and follow her. At the turn, she signals me to stop. She's looking across the hall.</p>\n<p>Éléa: I'll cover the surveillance camera, I'll be right behind me.<br>\nWamien: Upper left or right side camera?<br>\nÉléa: Right why?</p>\n<p>I stand against the wall and pass my little finger over the edge of the wall and pull a jammer nearby. Small electric arcs appear around the camera and then it goes down, broken.</p>\n<p>Wamien: That's why, let's go.<br>\nÉléa: Stay behind me anyway...</p>\n<p>She takes me through the most insignificant corridors of the whole building and we end up at the service exit door. She opens the door and comes out, I follow her. She closes the door behind me.</p>\n<p>Wamien: And my friends? Tom, Sam, Tiana? What are they gonna do without me?<br>\nÉléa: I Heum, I can't help that... I'm sorry. Find yourself a place to live outside the kingdom and avoid everyone, oh! And...</p>\n<p>She took out a small device which she placed around my wrist. She pushed a button and I felt an electric shock run through my wrist.</p>\n<p>Wamien: What did you just do?<br>\nÉléa: I just toasted the GPS they put in your wrist. Now you have to go.<br>\nWamien: To go where? You... you can't put me up for a few days.<br>\nÉléa: No, I'm sorry, I can't afford to put anyone up, and I have my reasons.<br>\nWamien: Ah, Heum, I see. All right, well, thanks, Éléa, take care.<br>\nÉléa: Thank you too Wamien, I hope to see you again one day.</p>\n<p>She left and I put my eyes back in normal mode. She is strange... Looks like she's not one of Darakei's experiments, but who could she be? I think back for a moment to Clara who had told me to come back to see her if anything ever happened. Yet that was three months ago. I hope she remembers me at least. I look at the lab and think back to my friends that I don't know if I'll ever see them again.</p>\n<p>An alarm is triggered. They probably saw I was missing. I start running to the next kingdom. I walk past people and mix very easily with the crowd despite my simple clothes. I go to a phone booth, I take out the little paper Clara had given me, I dial her phone number and it rings.</p>\n<p>Clara: Hello?</p>"}},"chapitreAvant":"chapter-9-the-usb-key","chapitreApres":"chapter-11-the-new-house","nomRoman":"the-first-cyborg-volume-1","slug":"chapter-10-elea","codeChapitre":"H0001C010","equivalentUrl":"/histoires/chapitre/chapitre-10-elea"}},"pathContext":{"slug":"chapter-10-elea"}}

